#portfolio {
  padding-top: 40px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.project {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.project div {
  margin: 20px;
}

.project span {
  color: #ff7b00;
  font-weight: 600;
}

.project .work_margin {
  margin-top: -10px;
}

.project_about p {
  max-width: 500px;
}

section {
  margin-bottom: 20px;
}

section article img {
  margin-left: -10px;
  padding: 10px;
}

section article img:active {
  opacity: .5;
}

.portfolio_img {
  -webkit-box-shadow: 1px 1px 2px silver;
          box-shadow: 1px 1px 2px silver;
  border-radius: 20px;
  width: 250px;
  height: 450px;
  padding: 1px;
  margin-bottom: 10px;
}

#drive {
  margin-bottom: 30px;
}

#car {
  margin-left: -30px;
}

#arrow:hover {
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

ul {
  list-style: none;
  margin-left: -45px;
  max-width: 500px;
}

ul li {
  display: inline-block;
  padding: 5px;
}

@media screen and (max-width: 768px){
    .project_about {
        max-width: 500px;
    }

    section article img {
        width: 280px;
        height: 200px;
    }

    section {
        margin-bottom: 5px;
    }
    
    #drive {
        margin-top: 10px;
    }
    
    #furelos {
        margin-top:-30px !important;
    }
}